  パンくずのスタイル設定
  ----------------
*/

#add .breadcrumb {
  .items {
    max-width: 1084px;
    padding-left: 30px;
    padding-right: 30px;
    margin: 0 auto;
    @media #{$sp} {
      padding-left: 15px;
      padding-right: 15px;
    }
    .item {

    }
    li {
      a {
        height: 100%;
        display: flex;
        color: #fff;
        font-size: 12px;
        font-size: 1.2rem;
        line-height: 2.08333;
        padding-top: 3px;
        padding-bottom: 3px;
        align-items: center;
        padding-left: 5px;
        img {
          width: 15px;
          height: auto;
        }
      }
    }
  }
}
