*/
#add {

  .more {
    
  }
  
  .tel {
    display: block;
    text-align: center;
    span {
      display: inline-block;
      color: #000;
      font-size: 32px;
      font-weight: 700;
      background: url('../img/common/phone.svg') left top 60% no-repeat;
      background-size: 25px auto;
      padding-left: 40px;
      @media #{$sp} {
        font-size: 24px;
        padding-left: 25px;
        background: url('../img/common/phone.svg') left top 60% no-repeat;
        background-size: 15px auto;
      }
    } 
  }

  .richBtn {
    text-align: center;
    &__small {
      position: relative;
      z-index: 9;
      display: inline-block;
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 10px;
      color: #fff;
      @media #{$sp} {
        font-size: 14px;
      }
    }
    &__ttl {
      text-align: center;
      position: relative;
      display: inline-block;
      margin-bottom: 10px;
      line-height: 1.3;
      color: #FFF033;
      font-size: 16px;
      font-weight: bold;
      @media #{$sp} {
        font-size: 14px;
      }
      &::before {
        content: "";
        position: absolute;
        left: -1.5em;
        bottom: 10px;
        display: inline-block;
        margin-right: 3px;
        width: 20px;
        height: 2px;
        background-color: #fff;
        transform: rotate(60deg);
        vertical-align: 0.3em;
      }
      &::after {
        content: "";
        position: absolute;
        right: -1.5em;
        bottom: 10px;
        display: inline-block;
        margin-left: 3px;
        width: 20px;
        height: 2px;
        background-color: #fff;
        transform: rotate(-60deg);
        vertical-align: 0.3em;
      }
      span {
        font-size: 20px;
        @media #{$sp} {
          font-size: 16px;
        }
      }
    }
    &__txt {
      margin-top: 10px;
      font-size: 14px;
      color: #fff;
      text-align: center;
      @media #{$sp} {
        font-size: 12px;
      }
    }
    .btn__type01 a,
    .btn__type01--white a {
      height: 91px;
      width: 500px;
      @media #{$sp} {
        width: 352px;
      }
    }
    .btn__type01--white a {
      &::before {
        border: 1px solid #fff;
      }
      .effect {
        border: 1px solid #fff;
      }
    }
    &:first-child {
      @media #{$sp} {
        margin-bottom: 30px;
      }
    }
  }

  
  
}

