/*
  ----------------
  common styles
  ----------------
*/
#add {

  /*** 表示・非表示 ***/


  .pc-only-inline-block {
    display: inline-block;
    @media #{$sp} {
      display: none;
    }
  }


  /*** FlexBox ***/

  .flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: space-between;
    @media #{$sp} {
      align-items: center;
      justify-content: center;
    }
    &.nowrap {
      flex-wrap: nowrap;
    }
    &.inherit {
      @media #{$sp} {
        justify-content: space-between;
        align-content: space-between;
        align-items: stretch;
      }
    }
    &.jcA {
      justify-content: space-around;
    }
    &.sp-jcA {
      @media #{$sp} {
        justify-content: space-around;
      }
    }
    &.jcC {
      justify-content: center;
    }
    &.fS {
      justify-content: flex-start;
    }
    &.sp-fS {
      @media #{$sp} {
        justify-content: flex-start;
      }
    }
    &.aiC {
      align-items: center;
    }
    &.aifS {
      align-items: flex-start;
    }
    &.sp-aifS {
      @media #{$sp} {
        align-items: flex-start;
      }
    }
    &.row-re {
      flex-direction: row-reverse;
    }
    &.sp-row-re {
      @media #{$sp} {
        flex-direction: row-reverse;
      }
    }
    &.sp-reverse {
      @media #{$sp} {
        flex-direction: column-reverse;
      }
    }
    &.sp-sB {
      @media #{$sp} {
        justify-content: space-between;
      }
    }
    &.gather {
      &:after {
        content:"";
        display: block;
        width:30.5%;
      }
    }
  }
  .sp-flex {
    @media #{$sp} {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-content: space-around;
    }
  }

  .core {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  /***********/

  a {
    transition: all .3s;
    color: #fff;
    // &:hover {
    //   opacity: .7;
    // }
  }

  /*** Container ***/

  .containers {
    max-width: $container;
    width: 100%;
    margin: 0 auto;
    padding: 70px 30px;
    @media #{$sp} {
      padding: 40px 15px;
    }
  }
  .containers-full {
    max-width: $container-full;
    width: 100%;
    margin: 0 auto;
    padding: 70px 0;
    @media #{$sp} {
      padding: 40px 15px;
    }
  }

  /*** Shadow ***/

  .box-shadow {
    box-shadow: 0 0 8px rgba(0,0,0,0.2);
  }
  .txt-shadow {
    text-shadow: 0 0 18px rgba(0,0,0,0.8);
  }

  /*** Scroll ***/

  .scroll {
    @media #{$sp} {
      overflow-x: scroll;
    }
  }

  /*** Clearfix ***/

  .cf:before,
  .cf:after {
    content:"";
    display:block;
    overflow:hidden;
  }
  .cf:after { clear:both; }
  .cf { 
    zoom:1;
    display: inline-table;
  }


  /*** その他 ***/

  main {
    padding-top: 140px;
    @media #{$sp} {
      padding-top: 60px;
    }
  }

  .taC {
    text-align: center;
  }

  .disabled {
    pointer-events: none;
  }

  .floating {
    position: fixed;
    top: 150px;
    right: 0;
    width: 300px;
    opacity: 0;
    transition: all .3s;
    display: none;
    z-index: 99;
    @media #{$sp} {
      display: none;
    }
    &.fixed {
      display: block;
      opacity: 1;
      @media #{$sp} {
        display: none;
      }
    }
  }
  
}



