/*
  ----------------
  paper-sol
  ----------------
*/

#add.paper-sol {

  .hero {
    background: url("../img/paper-sol/bg-hero.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/paper-sol/bg-hero-sp.webp") 50% / cover no-repeat;
    }
  }

  .intro {
    .lead {
      margin-bottom: 30px;
      @media #{$sp} {
        margin-bottom: 15px;
      }
    }
    .card {
      text-align: center;
      background: #fff;
      box-shadow: 0 0 10px rgba(7,15,38,0.2);
      padding: 30px;
      width: calc(80% / 2);
      margin: 0 20px;
      @media #{$sp} {
        padding: 80px 15px 15px;
        width: 47%;
        margin: 0 5px;
      }
      &__ttl {
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 10px;
        @media #{$sp} {
          font-size: 14px;
          margin-bottom: 0;
        }
      }
      &__txt {
        display: inline-block;
        span {
          display: inline-block;
          padding-left: 60px;
          position: relative;
          font-size: 51px;
          font-weight: bold;
          color: #083064;
          @media #{$sp} {
            padding-left: 0;
            font-size: 24px;
          }
          &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            @media #{$sp} {
              top: -80px;
              right: 0;
              bottom: auto;
            }
          }
          &.v1 {
            &::before {
              width: 35px;
              height: 44px;
              background: url("../img/paper-sol/img-intro01.svg") 50% / contain no-repeat;
            }
          }
          &.v2 {
            &::before {
              width: 44px;
              height: 46px;
              background: url("../img/paper-sol/img-intro02.svg") 50% / contain no-repeat;
            }
          }
          small {
            font-size: 20px;
            @media #{$sp} {
              font-size: 14px;
            }
          }
        }
      }
    }
  }

  .faq {
    background: #EEEEEE;
    .faqBox {
      margin-bottom: 10px;
      .question {
        position: relative;
        font-size: 20px;
        padding: 20px 60px;
        background: #F8F8F8;
        border: 1px solid #CCCCCC;
        background: #fff;
        cursor: pointer;
        @media #{$sp} {
          font-size: 16px;
          padding: 15px 30px;
        }
        &::before {
          content: "";
          position: absolute;
          top: 15px;
          left: 20px;
          width: 30px;
          height: 40px;
          background: url("../img/paper-sol/q.png") 50% / contain no-repeat;
          @media #{$sp} {
            top: 15px;
            left: 10px;
            width: 15px;
            height: 24px;
          }
        }
        &::after {
          content: "";
          position: absolute;
          top: 20px;
          right: 30px;
          width: 30px;
          height: 30px;
          background: url("../img/paper-sol/faq-arrow.svg") 50% / contain no-repeat;
          transform: rotate(-180deg);
          transition: all .3s;
          @media #{$sp} {
            top: 15px;
            right: 10px;
            width: 20px;
            height: 20px;
          }
        }
        &.open {
          &::after {
            transform: rotate(0deg);
          }
        }
      }
      .answer {
        display: none;
        font-size: 16px;
        padding: 20px 40px;
        background: #F8F8F8;
        border: 1px solid #CCCCCC;
        @media #{$sp} {
          font-size: 14px;
          padding: 15px 20px;
        }
      }
    }
  }

  .reason {
    background: url("../img/common/bg_texture01.svg") 0 repeat;
    .card {
      position: relative;
      background: #fff;
      box-shadow: 0 0 10px rgba(7,15,38,0.2);
      padding: 40px;
      @media #{$sp} {
        padding: 40px 15px 20px;
        justify-content: center;
        margin-top: 40px;
      }
      &:not(:last-child) {
        margin-bottom: 20px;
        @media #{$sp} {
          margin-bottom: 15px;
        }
      }
      &__num {
        color: #E91313;
        font-size: 64px;
        font-weight: bold;
        font-family: "din-2014", sans-serif;
        width: 10%;
        @media #{$sp} {
          text-align: center;
          width: 100%;
          position: absolute;
          top: -50px;
        }
      }
      &__ttl {
        width: calc(45% - 50px);
        margin-right: 50px;
        @media #{$sp} {
          width: 100%;
          margin-right: 0;
          margin-bottom: 20px;
        }
        &.row {
          width: calc(100% - 10%);
          margin-right: 0;
          @media #{$sp} {
            width: 100%;
          }
        }
        .ttl-tertiary {
          text-align: left;
          @media #{$sp} {
            text-align: center;
          }
        }
        .lead {
          text-align: left;
          font-size: 16px;
          @media #{$sp} {
            font-size: 14px;
          }
        }
        &--summry {
          font-size: 14px;
          margin-top: 10px;
          .note {
            color: #FF0000;
            font-size: 12px;
            margin-top: 5px;
          }
        }
        &--note {
          color: #E91313;
          font-size: 12px;
          margin-top: 5px;
        }
      }
      &__img {
        width: 45%;
        @media #{$sp} {
          width: 100%;
        }
        &.full {
          width: 100%;
          margin-top: 50px;
          @media #{$sp} {
            margin-top: 0;
          }
        }
      }
    }
  }

  .case-study {
    background: url("../img/common/bg_texture03.svg") 0 repeat;
    .lead {
      margin-bottom: 40px;
    }
    .card {
      width: calc(100% / 2 - 20px);
      display: flex;
      flex-direction: column;
      background: #fff;
      padding: 40px;
      position: relative;
      @media #{$sp} {
        width: 100%;
        padding: 40px 15px 20px;
      }
      &::before {
        content: "";
        position: absolute;
        top: -25px;
        left: 0;
        right: 0;
        margin: auto;
        width: 50px;
        height: 50px;
        background: url("../img/paper-sol/icon-check.svg") 50% / contain no-repeat;
        @media #{$sp} {
          width: 40px;
        height: 40px;
        top: -20px;
        }
      }
      &:not(:last-child) {
        @media #{$sp} {
          margin-bottom: 50px;
        }
      }
      &__ttl {
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 30px;
        line-height: 1.2;
        height: 70px;
        @media #{$sp} {
          font-size: 18px;
          margin-bottom: 15px;
          height: auto;
        }
      }
      &__img {
        margin-top: auto;
      }
      &__txt {
        text-align: left;
        margin-bottom: 40px;
        @media #{$sp} {
          margin-bottom: 20px;
        }
      }
    }
  }

  .please {
    background: #830009;
    &__ttl {
      font-size: 16px;
      font-weight: bold;
      color: #fff;
      text-align: center;
      background: #000;
      border-radius: 37px;
      padding: 10px;
      width: 220px;
      margin: 0 auto 30px;
      position: relative;
      @media #{$sp} {
        font-size: 14px;
        width: 200px;
        margin: 0 auto 20px;
      }
      span {
        font-size: 20px;
        color: #FFF033;
        @media #{$sp} {
          font-size: 16px;
        }
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 100%;
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 8px 6px 0 6px;
        border-color: #000 transparent transparent transparent;
      }
    }
    &__lead {
      font-size: 18px;
      font-weight: bold;
      text-align: center;
      color: #fff;
      margin-bottom: 10px;
      @media #{$sp} {
        font-size: 14px;
      }
      span {
        font-size: 20px;
        border-bottom: 2px solid #fff;
        @media #{$sp} {
          font-size: 16px;
        }
      }
    }
    &__txt {
      font-size: 24px;
      font-weight: bold;
      text-align: center;
      color: #fff;
      margin-bottom: 40px;
      @media #{$sp} {
        font-size: 18px;
        margin-bottom: 20px;
      }
      span {
        color: #FFF033;
      }
    }
  }
  
}