/*
  ----------------
  common
  ----------------
*/

#add {
  
  .task {
    background: #EEEEEE;
    position: relative;
    .ttl-secondary {
      margin-bottom: 120px;
      @media #{$sp} {
        margin-bottom: 25px;
      }
    }
    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      margin: auto;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 50px 80px 0 80px;
      border-color: #eee transparent transparent transparent;
      @media #{$sp} {
        border-width: 25px 40px 0 40px;
        border-color: #eee transparent transparent transparent;
      }
    }
    &__list {
      width: calc(100% / 3 - 20px);
      position: relative;
      @media #{$sp} {
        width: calc(100% - 20px);
        margin-left: 20px;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 20px;
        margin: auto;
        width: 30px;
        height: 16px;
        background: url("../img/common/task-arrow.svg") 50% / contain no-repeat;
        @media #{$sp} {
          content: none;
        }
      }
      &:not(:last-child) {
        @media #{$sp} {
          margin-bottom: 15px;
        }
      }
      &--ttl {
        position: absolute;
        left: 0;
        right: 0;
        top: -65px;
        margin: auto;
        @media #{$sp} {
          right: auto;
          top: 26%;
          bottom: auto;
          left: -30px;
        }
        span {
          display: inline-block;
          background: #333;
          color: #fff;
          text-align: center;
          padding: 3px;
          width: 140px;
          border-radius: 5px;
          font-size: 20px;
          font-weight: bold;
          @media #{$sp} {
            width: 60px;
            font-size: 12px;
            border: 2px solid #EEEEEE;
          }
        }
      }
      &--box {
        display: block;
        text-align: center;
        background: #fff;
        height: 400px;
        box-shadow: 0 0 10px rgba(7,15,38,0.2);
        color: #000;
        @media #{$sp} {
          height: auto;
          display: flex;
        }
        figure {
          overflow: hidden;
          @media #{$sp} {
            width: 35%;
          }
          img {
            transition: all .3s;
          }
        }
        summary {
          font-size: 20px;
          font-weight: bold;
          line-height: 1.35;
          padding: 20px;
          @media #{$sp} {
            display: flex;
            align-items: center;
            position: relative;
            width: 65%;
            font-size: 12px;
            padding: 10px 30px 10px 10px;
            text-align: left;
            &::before {
              content: "";
              position: absolute;
              margin: auto;
              bottom: 0;
              top: 0;
              right: 10px;
              width: 20px;
              height: 10px;
              background: url("../img/common/task-arrow.svg") 50% / contain no-repeat;
            }
          }
        }
        &:hover {
          figure img {
            transform:scale(1.1);
          }
        }
      }
    }
  }

  .feature {
    background: url("../img/common/bg_texture01.svg") 0 repeat;
    .title__type01 {
      margin-bottom: 60px;
      @media #{$sp} {
        margin-bottom: 40px;
      }
    }
    .card {
      position: relative;
      background: #fff;
      box-shadow: 0 0 10px rgba(7,15,38,0.2);
      padding: 60px;
      @media #{$sp} {
        padding: 60px 15px 30px;
      }
      &:not(:last-of-type) {
        margin-bottom: 60px;
        @media #{$sp} {
          margin-bottom: 40px;
        }
      }
      .lead {
        font-size: 16px;
        margin-bottom: 30px;
        @media #{$sp} {
          font-size: 14px;
          margin-bottom: 15px;
        }
      }
      &__ttl {
        background: #E91313;
        border-radius: 5px;
        color: #fff;
        text-align: center;
        padding: 10px;
        font-size: 16px;
        font-weight: bold;
        line-height: 1.4;
        position: absolute;
        width: 600px;
        left: 0;
        right: 0;
        top: -20px;
        margin: auto;
        @media #{$sp} {
          font-size: 14px;
          width: 85%;
          padding: 5px;
        }
      }
      &__img {
        margin: 0 auto;
        width: 80%;
        @media #{$sp} {
          width: 100%;
        }
      }
    }
    .item {
      margin-top: 60px;
      @media #{$sp} {
        margin-top: 30px;
      }
      &__box {
        text-align: center;
        position: relative;
        padding: 30px;
        background: #F9F9F9;
        width: calc(100% / 3 - 20px);
        @media #{$sp} {
          width: 100%;
        }
        &:not(:last-child) {
          @media #{$sp} {
            margin-bottom: 50px;
          }
          &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: -25px;
            margin: auto;
            width: 25px;
            height: 25px;
            background: url("../img/rfid-sol/img-arrow.svg") 50% / contain no-repeat;
            @media #{$sp} {
              top: auto;
              bottom: -25px;
              left: 0;
              right: 0;
              background: url("../img/rfid-sol/img-arrow-sp.svg") 50% / contain no-repeat;
            }
          }
        }
        &--ttl {
          font-size: 20px;
          font-weight: bold;
          color: #fff;
          background: #333;
          padding: 7px;
          border-radius: 5px;
          width: 140px;
          position: absolute;
          left: 0;
          right: 0;
          top: -20px;
          margin: auto;
          @media #{$sp} {
            font-size: 16px;
            width: 120px;
            top: -15px;
          }
          &.bg-R {
            background: #9F2020;
          }
        }
        &--img {
          margin: 30px 0;
          @media #{$sp} {
            margin: 15px 0;
          }
          img {
            max-width: none;
            width: auto;
            height: 82px;
            @media #{$sp} {
              height: 60px;
            }
          }
        }
        &--txt {
          text-align: left;
          font-size: 16px;
          @media #{$sp} {
            font-size: 14px;
          }
        }
      }
      
    }
  }

  .material {
    
    .containers {
      padding: 40px 30px;
      @media #{$sp} {
        padding: 40px 15px;
      }
    }
    &__img {
      width: 40%;
      text-align: right;
      @media #{$sp} {
        width: 85%;
        text-align: center;
        margin-bottom: 15px;
      }
      img {
        max-width: none;
        width: 70%;
      }
    }
    &__box {
      width: 400px;
      margin-left: 40px;
      text-align: center;
      @media #{$sp} {
        width: 100%;
        margin-left: 0;
      }
      &--txt {
        text-align: center;
        position: relative;
        display: inline-block;
        margin-bottom: 10px;
        line-height: 1.3;
        color: #FFF033;
        font-size: 16px;
        @media #{$sp} {
          font-size: 14px;
        }
        &::before {
          content: "";
          position: absolute;
          left: -1.5em;
          bottom: 10px;
          display: inline-block;
          margin-right: 3px;
          width: 20px;
          height: 2px;
          background-color: #fff;
          transform: rotate(60deg);
          vertical-align: 0.3em;
        }
        &::after {
          content: "";
          position: absolute;
          right: -1.5em;
          bottom: 10px;
          display: inline-block;
          margin-left: 3px;
          width: 20px;
          height: 2px;
          background-color: #fff;
          transform: rotate(-60deg);
          vertical-align: 0.3em;
        }
        span {
          font-size: 20px;
          @media #{$sp} {
            font-size: 16px;
          }
        }
      }
      &--ttl {
        display: inline-block;
        font-size: 25px;
        font-weight: bold;
        color: #fff;
        margin-bottom: 25px;
        @media #{$sp} {
          margin-bottom: 15px;
          font-size: 18px;
        }
      }
    }
  }

  .brand {
    &__intro {
      margin-bottom: 70px;
      @media #{$sp} {
        margin-bottom: 35px;
      }
    }
    .lead {
      width: calc(100% / 2 - 40px);
      text-align: left;
      @media #{$sp} {
        width: 100%;
      }
    }
    .card {
      width: calc(100% / 3 - 20px);
      border: 1px solid #CCCCCC;
      margin-bottom: 30px;
      @media #{$sp} {
        width: 100%;
        margin-bottom: 20px;
      }
      &:last-child {
        @media #{$sp} {
          margin-bottom: 40px;
        }
      }
      &__ttl {
        height: 90px;
        padding: 20px;
        background-color:rgba(230,0,17,0.1);
        font-weight: bold;
        @media #{$sp} {
          padding: 5px;
          height: 80px;
        }
        span {
          display: inline-block;
          padding-left: 50px;
          text-align: left;
          @media #{$sp} {
            padding-left: 0;
            padding-top: 33px;
            display: block;
          }
          &.num01 {
            background: url("../img/common/num01.svg") 0 50% / 35px no-repeat;
            @media #{$sp} {
              background: url("../img/common/num01.svg") 50% 0 / 35px no-repeat;
            }
          }
          &.num02 {
            background: url("../img/common/num02.svg") 0 50% / 35px no-repeat;
            @media #{$sp} {
              background: url("../img/common/num02.svg") 50% 0 / 35px no-repeat;
            }
          }
          &.num03 {
            background: url("../img/common/num03.svg") 0 50% / 35px no-repeat;
            @media #{$sp} {
              background: url("../img/common/num03.svg") 50% 0 / 35px no-repeat;
            }
          }
          &.num04 {
            background: url("../img/common/num04.svg") 0 50% / 35px no-repeat;
            @media #{$sp} {
              background: url("../img/common/num04.svg") 50% 0 / 35px no-repeat;
            }
          }
          &.num05 {
            background: url("../img/common/num05.svg") 0 50% / 35px no-repeat;
            @media #{$sp} {
              background: url("../img/common/num05.svg") 50% 0 / 35px no-repeat;
            }
          }
        }
      }
      &__box {
        padding: 25px;
        @media #{$sp} {
          padding: 20px;
        }
        &--img {
          text-align: center;
          margin-bottom: 15px;
          img {
            max-width: none;
            width: auto;
            height: 75px;
          }
        }
        &--txt {

        }
      }
    }
    &__ttl {
      font-size: 20px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 30px;
      @media #{$sp} {
        font-size: 16px;
        margin-bottom: 15px;
      }
    }
    &__img {

    }
  }

  

  .info {
    background: #A50005;
    color: #fff;
    .ttl-secondary {
      margin-bottom: 30px;
      @media #{$sp} {
        margin-bottom: 15px;
      }
    }
    &__lead {
      text-align: center;
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 40px;
      @media #{$sp} {
        margin-bottom: 20px;
        font-size: 16px;
      }
    }
    &__box {
      background: #fff;
      color: #000;
      padding: 30px;
      width: 80%;
      margin: 0 auto;
      @media #{$sp} {
        padding: 15px;
        width: 100%;
      }
      &--btn {
        margin: 0 30px;
        @media #{$sp} {
          margin: 0 0 15px 0;
        }
      }
      &--txt {
        margin: 0 30px;
        @media #{$sp} {
          margin: 0 0 0 0;
        }
        .info-time {
          font-size: 18px;
          text-align: center;
          @media #{$sp} {
            font-size: 14px;
          }
        }
      }
    }
  }

}

.contact__list {
  margin-bottom: 30px;
  @media #{$sp} {
    margin-bottom: 15px;
  }
  &--box {
    max-width: 800px;
    margin: 0 auto;
    .ttl {
      font-size: 24px;
      font-weight: 600;
      line-height: 1;
      letter-spacing: .01em;
      margin-bottom: 15px;
      text-align: center;
      @media #{$sp} {
        font-size: 18px;
        margin-bottom: 10px;
      }
    }
    .txt {
      li {
        font-size: 14px;
        line-height: 2;
        letter-spacing: .038em;
        padding-left: 2em;
        position: relative;
        list-style: none;
        @media #{$sp} {
          font-size: 14px;
        }
        &::before {
          content: "●";
          position: absolute;
          top: 0;
          left: 0.5em;
          color: #E60011;
        }
        &:first-child {
          font-size: 20px;
          line-height: 2;
          letter-spacing: .038em;
          font-weight: bold;
          padding-left: 0;
          @media #{$sp} {
            font-size: 16px;
          }
          &::before {
            content: none;
          }
        }
      }
      &:not(:last-child) {
        margin-bottom: 15px;
        @media #{$sp} {
          margin-bottom: 10px;
        }
      }
    }
    dd.txt {
      @media #{$sp} {
        margin: 0;
      }
    }
  }
}