/*
  ----------------
  mold
  ----------------
*/

#add.mold {

  .hero {
    background: url("../img/mold/bg-hero.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/mold/bg-hero-sp.webp") 50% / cover no-repeat;
    }
  }

  .material {
    background: url("../img/mold/img-material.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/mold/img-material-sp.webp") 50% / cover no-repeat;
    }
  }

  .doc {
    background: url("../img/rfid-sol/bg-doc.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/rfid-sol/bg-doc.webp") 0 0 / cover no-repeat;
    }
    .title__type01 {
      color: #000;
      b {
        font-size: 30px;
        font-weight: bold;
        @media #{$sp} {
          font-size: 22px;
        }
        // &.cB {
        //   color: #083064;
        // }
        // &.cR {
        //   color: #A50005;
        // }
      }
    }
    .card {
      background: #fff;
      box-shadow: 0 0 10px rgba(7,15,38,0.2);
      padding: 60px;
      color: #000;
      @media #{$sp} {
        width: 100%;
        padding: 15px;
      }
      &__img {
        width: 40%;
        @media #{$sp} {
          width: 75%;
          margin: 0 auto 15px;
        }
      }
      &__box {
        width: calc(100% - 40% - 60px);
        @media #{$sp} {
          width: 100%;
        }
        &--txt {
          margin-bottom: 30px;
          @media #{$sp} {
            margin-bottom: 15px;
          }
        }
        &--btn {
          background: #4C4C4C;
          color: #fff;
          text-align: center;
          font-weight: bold;
          display: block;
          padding: 20px;
          transition: all .3s;
          @media #{$sp} {
            padding: 15px;
          }
          span {
            padding-right: 40px;
            position: relative;
            &::before {
              content: "";
              position: absolute;
              top: 0;
              bottom: 0;
              right: 0;
              margin: auto;
              width: 21px;
              height: 21px;
              background: url("../../images/ico_arrow01_white.svg") 50% / contain no-repeat;
            }
          }
        }
      }
      &:hover {
        .card__box--btn {
          background: #E60011;
        }
      }
    }
  }

  

  .brand .card {
    margin-bottom: 70px;
    @media #{$sp} {
      margin-bottom: 30px;
    }
  }

}

