/*
  ----------------
  rfid-sol
  ----------------
*/

#add.rfid-sol {

  .hero {
    background: url("../img/rfid-sol/bg-hero.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/rfid-sol/bg-hero-sp.webp") 50% / cover no-repeat;
    }
  }

  .doc {
    background: url("../img/mold/bg-doc.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/mold/bg-doc.webp") 0 0 / cover no-repeat;
    }
    .flex {
      gap: 40px;
      @media #{$sp} {
        gap: 20px;
      }
    }
    .card {
      display: block;
      width: calc(100% / 2 - 20px);
      background: #fff;
      box-shadow: 0 0 10px rgba(7,15,38,0.2);
      padding: 30px;
      color: #000;
      @media #{$sp} {
        width: 100%;
      }
      &__ttl {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 40px;
        text-align: center;
        @media #{$sp} {
          font-size: 18px;
          margin-bottom: 20px;
        }
        span {
          position: relative;
          @media #{$sp} {
            display: block;
          }
          &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: -30px;
            margin: auto;
            width: 18px;
            height: 17px;
            background: url("../img/mold/doc-line-l.svg") 50% / contain no-repeat;
            @media #{$sp} {
              left: 0;
            }
          }
          &::after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: -30px;
            margin: auto;
            width: 18px;
            height: 17px;
            background: url("../img/mold/doc-line-r.svg") 50% / contain no-repeat;
            @media #{$sp} {
              right: 0;
            }
          }
        }
      }
      &__img {
        width: 75%;
        margin: 0 auto 30px;
        @media #{$sp} {
          margin: 0 auto 15px;
        }
      }
      &__txt {
        width: 75%;
        margin: 0 auto 30px;
        @media #{$sp} {
          margin: 0 auto 15px;
        }
      }
      &__btn {
        background: #4C4C4C;
        color: #fff;
        text-align: center;
        font-weight: bold;
        display: block;
        padding: 20px;
        transition: all .3s;
        @media #{$sp} {
          padding: 15px;
        }
        span {
          padding-right: 40px;
          position: relative;
          &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
            width: 21px;
            height: 21px;
            background: url("../../images/ico_arrow01_white.svg") 50% / contain no-repeat;
          }
        }
      }
      &:hover {
        .card__btn {
          background: #E60011;
        }
      }
    }
  }

  .material {
    background: url("../img/rfid-sol/img-material.webp") 50% / cover no-repeat;
    @media #{$sp} {
      background: url("../img/rfid-sol/img-material-sp.webp") 50% / cover no-repeat;
    }
    &__box {
      width: calc(100% - 40% - 40px);
      @media #{$sp} {
        width: 100%;
      }
    }
  }

  .brand__intro {
    text-align: center;
  }

  .brand .card.col2 {
    margin: 0 15px 30px;
    @media #{$sp} {
      margin: 0 auto 20px;
    }
  }

  .cost {
    background: url("../img/common/bg_texture03.svg") 0 repeat;
    &__img {
      margin-top: 40px;
      @media #{$sp} {
        margin-top: 20px;
      }
    }
  }

  .step {
    background: url("../img/common/bg_texture05.svg") 0 repeat;
    &__img {
      width: calc(100% / 2);
      @media #{$sp} {
        width: 100%;
      }
      &:first-child {
        margin-right: -7px;
        @media #{$sp} {
          margin-right: 0;
          margin-bottom: 40px;
        }
      }
      &:last-child {
        margin-left: -7px;
        @media #{$sp} {
          margin-left: 0;
        }
      }
    }
  }

  .package {
    background: url("../img/rfid-sol/bg-package.webp") 50% / cover no-repeat;
    color: #fff;
    @media #{$sp} {
      background: url("../img/rfid-sol/bg-package-sp.webp") 50% / cover no-repeat;
    }
    .title__type01 small {
      font-size: 20px;
      font-weight: bold;
      @media #{$sp} {
        font-size: 16px;
      }
    }
    .item {
      &__box {
        width: calc(100% / 2);
        padding: 0 60px;
        @media #{$sp} {
          width: 100%;
          padding: 0;
        }
        &--ttl {
          font-size: 25px;
          font-weight: bold;
          margin-bottom: 30px;
          text-align: center;
          @media #{$sp} {
            font-size: 18px;
            margin-bottom: 20px;
          }
        }
        &--img {
          display: block;
          margin-bottom: 30px;
          @media #{$sp} {
            margin-bottom: 20px;
          }
        }
        &--txt {
          font-size: 16px;
          @media #{$sp} {
            font-size: 14px;
          }
        }
        &:hover {
          .item__box--img {
            opacity: .7;
          }
        }
        &:first-child {
          @media #{$sp} {
            margin-bottom: 30px;
          }
        }
      }
    }
  }

}

