/*
  ----------------
  各　見出し
  ----------------
*/
#add {

  .ttl-primary {
    font-size: 40px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    margin-bottom: 30px;
    @media #{$sp} {
      font-size: 25px;
      margin-bottom: 15px;
    }
  }

  .ttl-sub {
    color: #fff;
    font-size: 20px;
    text-align: center;
    margin-bottom: 50px;
    @media #{$sp} {
      font-size: 14px;
      margin-bottom: 25px;
    }
  }
  
  .ttl-secondary {
    font-size: 30px;
    font-size: 3rem;
    font-weight: bold;
    line-height: 1.33333;
    margin-bottom: 50px;
    text-align: center;
    @media #{$sp} {
      margin-bottom: 20px;
      font-size: 20px;
      font-size: 2.0rem;
    }
    &.cW {
      color: #fff;
    }
  }
  
  .ttl-tertiary {
    font-size: 25px;
    font-weight: bold;
    color: #000;
    text-align: center;
    line-height: 1.2;
    margin-bottom: 20px;
    @media #{$sp} {
      font-size: 16px;
      margin-bottom: 15px;
    }
  }
  
  .ttl-quaternary {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    position: relative;
    @media #{$sp} {
      font-size: 14px;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      max-width: 1100px;
      width: 100%;
      height: 1px;
      border-bottom: 1px dashed #000;
      z-index: 0;
    }
    span {
      display: inline-block;
      background: #fff;
      padding: 0 2em;
      position: relative;
      z-index: 1;
      @media #{$sp} {
        padding: 0 1.5em;
      }
    }
    &.mt40 {
      margin-top: 40px;
      @media #{$sp} {
        margin-top: 20px;
      }
    }

  }
  
  .ttl-quinary {
    
  }
  
  .lead {
    text-align: center;
    line-height: 1.625;
  }
  
  .note {
    
  }

  .title__type01 {
    font-weight: bold;
    text-align: center;
    font-size: 30px;
    @media #{$sp} {
      font-size: 20px;
    }
    b {
      font-size: 38px;
      @media #{$sp} {
        font-size: 24px;
      }
    }
  }

  .wp__main-title {
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 30px;
    @media #{$sp} {
      font-size: 24px;
      margin-bottom: 15px;
    }
  }

  .info__form .privacy_policy a {
    display: inline;
    text-decoration: underline;
    color: #1111cc;
  }

  .item-title,.items {
    line-height: 2;
  }

  .wp__rec .title {
    text-align: center;
    font-size: 20px;
    font-size: 2rem;
    line-height: 1.6;
    color: #E60011;
    margin-top: 0;
    margin-bottom: 30px;
    font-weight: bold;
    @media #{$sp} {
      font-size: 16px;
      margin-bottom: 15px;
    }
  }

  .info__form .button_area input[type=button] {
    cursor: pointer;
    font-size: medium;
    background: none;
    padding: 24px 0 24px 2em;
    color: #fff;
    font-weight: 700;
    border: 0;
    width: 100%;
    font-size: 16px;
    margin: 30px 0 0;
    background: url("../img/common/btn_download.svg") center no-repeat;
    background-size: contain;
    max-width: 346px;
    @media #{$sp} {
      padding: 24px 0 24px 2.5em;
      font-size: 14px;
    }
  }

  .wp__container .info__form {
    @media #{$sp} {
      margin-top: 0;
    }
  }

}

